* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Muli', sans-serif; }


body {
    background-color: #f6f7f8;
    color: #373535; }

.product-list-page {
    position: relative; }

.product {
    position: relative;
    height: 400px;
    max-width: 300px;
    display: flex;
    flex-wrap: wrap;
    border-radius: 5px;
    border: 1px solid #ddd;
    background-color: #FFFFFF;
    transition: all .5s ease-out;
    cursor: pointer;
    margin: 0 auto 30px auto;
    &:hover {
        box-shadow: 0 13px 21px -5px rgba(0,0,0,.2);
        .product__thumb:after {
            opacity: .5;
            visibility: visible; }
        .product__compare-btn {
            visibility: visible;
            opacity: 1; } } }


.product__thumb {
    position: absolute;
    width: 100%;
    &:after {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: #e18c4e;
        opacity: 0;
        visibility: hidden;
        border-radius: 5px 5px 0 0;
        transition: all .3s ease-out; } }

.product__thumb--compare-remove:after {
    opacity: .5;
    visibility: visible; }

.produc__img {
    width: 100%;
    border-radius: 5px; }

.product__compare-btn {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: transparent;
    font-size: 22px;
    border: 2px solid #FFFFFF;
    color: #FFFFFF;
    padding: 5px 15px;
    z-index: 2;
    transition: all .3s ease-out;
    visibility: hidden;
    opacity: 0; }

.product__compare-btn--remove {
    visibility: visible;
    opacity: 1; }

.product__text {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-self: flex-end;
    padding: 10px 15px;
    margin-bottom: 20px;
    position: relative;
    background-color: #FFFFFF; }

.product__title {
    flex-basis: 60%;
    font-size: 22px;
    margin: 0; }

.product__short-description {
    flex-basis: 1000%;
    order: 3;
    font-size: 15px;
    color: #b9b6b6;
    margin: 0; }

.product__price {
    flex-basis: 40%;
    order: 2;
    text-align: right;
    font-size: 22px;
    color: #e18c4e;
    font-weight: 600; }

.compare {
    width: 100%;
    background-color: #FFFFFF;
    position: fixed;
    max-width: 1500px;
    bottom: -100%;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 5px 5px 0 0;
    border: 1px solid #ddd;
    box-shadow: 0 1px 11px -5px rgba(0,0,0,.2);
    overflow: hidden;
    overflow-x: scroll;
    scrollbar-width: none;
    z-index: 3;
    animation: compare-bar 1s forwards; }

@keyframes compare-bar {
  from {
      bottom: -100%; }
  to {
      bottom: 0%; } }


.compare__row {
    display: flex;
    list-style: none;
    margin: 0; }

.compare__param {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 20px 10px;
    width: calc(100% / 4);
    min-width: 172px;
    font-size: 22px;
    margin: 0;
    border-bottom: 1px solid #ddd; }

.compare__param--head {
    background-color: #f6f7f8; }

.compare__img {
    flex-basis: 40px; }

.compare__single-color {
    border-radius: 100%;
    height: 20px;
    width: 20px;
    border: 5px;
    margin: 3px; }

.compare__param-condition {
    color: #FFFFFF;
    font-weight: 600;
    border-bottom: none; }

.compare__param-condition--fresh {
    background-color: #4db04d; }

.compare__param-condition--frozen {
    background-color: #4f94ff; }
